import 'Verselist.css'
import verseData from "verseData"
import VerseListItem from 'VerseListItem'
import queryString from 'query-string'
import { isMobile } from 'globals'



export default class VerseList extends React.Component {
	constructor(p){
		super(p)
		let {listBg, verseList, verse} = this
	}
	setBackground(e){
		let { listBg, verseList } = this
		let header = document.getElementsByTagName('header')
		let id = e.currentTarget.getAttribute('data-id')

		if(!isMobile){
			listBg.style.backgroundImage = `url(${process.env.PUBLIC_PATH}asset/${id}.jpg)`
			listBg.style.transition = '.5s'
			listBg.style.opacity = '.5'
			header[0].style.backgroundColor = 'transparent'
		}
	}
	noBackground = () =>{
		let { listBg, verseList } = this
		let header = document.getElementsByTagName('header')

		listBg.style.transition = '.5s'
		listBg.style.opacity= '0'
		header[0].style.backgroundColor = '#EBEBEC'
	}
    render() {
        return  <section className="verselist page" ref={ el => this.verseList = el }>
                <div className="listBg" 
                	 ref={ el => this.listBg = el }>
                </div>
                {verseData == null ? null : 
                    verseData.map((e, id) => <VerseListItem key={id} item={e} 
                    										onMouseOver={(e) => this.setBackground(e)} 
                    										onMouseLeave={this.noBackground}
                    										/>)}

        </section>
    }
}


